.login_box{
  display: flex;
  width: 500px;
  height: auto;
  border: 1px solid blue;
  flex-direction: column;
  align-items: center;
  padding:50px;
  animation-name: exist;
  animation-duration: 1.5s;
  animation-timing-function: ease;
}

@keyframes exist {
    0% {margin-top: 50px; opacity: 0}
    100%{margin-top: 0px; opacity: 1}
}

.Login_button {
  width: 100px;
  background-color: rgba(49,93,223,1);
  color: white;
  border: none;
  padding: 15px;
  border-radius: 10px;
}

.input {
  height: 20px;
  border: 1px solid grey;
  border-radius: 5px;
  width: 50%;
  margin: 10px;
}
